html,
body {
  margin: 0;
  height: 100vh;
  width: 100vw;
  background: radial-gradient(
    circle,
    rgba(255, 235, 214, 1) 21%,
    rgba(250, 201, 169, 1) 93%
  );
}

.App {
  position: absolute;
  height: 100vh;
  width: 100vw;
}
.pos {
  display: flex;
  justify-content: center;
  /* position: absolute; */
}
@font-face {
  font-family: "digital-7";
  src: local("digital-7"), url(./digital-7.ttf) format("truetype");
}
.display {
  position: absolute;
  height: 10vh;
  width: 40vw;
  z-index: 1;
  background-color: lightgray;
  margin: 10px;
  box-shadow: inset 0px 0px 10px -3px darkgray;
  font-size: 9vh;
  color: rgb(49, 49, 49);
  font-family: digital-7;
  text-align: right;
  padding-right: 5px;
}
.result {
  position: absolute;
  height: 5vh;
  width: 20vw;
  z-index: 1;
  background-color: rgb(226, 226, 226);
  right: 0%;
  bottom: 0%;
  margin: 20px;
  text-align: right;
  font-size: 5vh;
  padding-right: 5px;
  box-shadow: inset 0px 0px 10px -3px darkgray;
  font-family: digital-7;
}
.preres {
  position: absolute;
  display: flex;
  height: 50vh;
  width: 20vw;
  z-index: 1;
  right: 0%;
  bottom: 3%;
  margin: 20px;
  text-align: right;
  align-items: flex-end;
  justify-content: flex-end;
  color: gray;
}
.credit {
  position: absolute;
  bottom: 2%;
  left: 5%;
  font-size: 15px;
}
